<template>
  <div class="container-fluid bg-dark-cust">
    <Navbar />
    <Alert></Alert>
    <loading :active.sync="isLoading"></loading>
    <!--  -->
    <div >
      <div class="row justify-content-center ">
        <div class="col-md-6 col-lg-5 ">
          <div class="my-5">
            <div
              class="row text-light justify-content-between text-center pb-5"
              style="border-bottom:2px solid #FFFFFF">
              <div class="col-2">
                <i class="fas far fa-check-circle fa-3x text-primary"></i>
                <p class="mt-2 text-primary"></p>
              </div>
              <div class="col-1">
                <i class="fas fa-arrow-right fa-2x mt-3"></i>
              </div>
              <div class="col-2 text-warning">
                <i class="far fa-id-card fa-3x"></i>
                <p class="mt-2"></p>
              </div>
              <div class="col-1">
                <i class="fas fa-arrow-right fa-2x mt-3"></i>
              </div>
              <div class="col-2">
                <i class="fas fa-calendar-check fa-3x "></i>
                <p class="mt-2"></p>
              </div>
            </div>
          </div>
          <h6 class="h3 text-white text-center mb-3"></h6>
          <form @submit.prevent="CreatOrder()">
            
            <div class="form-group">
              <label for="Name">:</label>
              <input type="text" class="form-control" v-validate="'required'" :class="{'is-invalid':errors.has('name')}" name="name" id="Name" v-model="form.user.name"  placeholder="" />
              <span class="text-danger " v-if="errors.has('name')"></span>
            </div>
            <div class="form-group">
              <label for="tel">:</label>
              <input type="text" class="form-control"  v-validate="'required|digits:10'" :class="{'is-invalid':errors.has('tel')}" id="tel" name="tel" v-model="form.user.tel" placeholder="" />
              <span class="text-danger" v-if="errors.has('tel')">10</span>
             
            </div>
            <div class="form-group">
              <label for="email">Email:</label>
              <input type="email" class="form-control"  v-validate="'required|email'" :class="{'is-invalid':errors.has('email')}" name="email" id="Email" v-model="form.user.email" placeholder="Email" />
              <span class="text-danger " v-if="errors.has('email')">{{errors.first('email')}}</span>
            </div>
            <div class="form-group">
              <label for="address">:</label>
              <input type="text" class="form-control"   v-validate="'required'" :class="{'is-invalid':errors.has('adress')}"  id="adress" name="adress" v-model="form.user.adress" placeholder="" />
              <span class="text-danger " v-if="errors.has('adress')"></span>
            </div>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">:</label>
              <textarea class="form-control" id="exampleFormControlTextarea1" v-model="form.message" rows="3"></textarea>
            </div>
            <div class="d-flex justify-content-center">
              <button type="submit" class="btn  btn-danger" ></button>
            </div> 
          </form>
          <!-- () -->
          <h6 class="h3 text-white text-center my-4"></h6>
        <div class="accordion" id="accordionExample">
        <div class="d-flex justify-content-center">
        <button class="btn btn-danger" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">ATM</button>
        <button class="btn btn-danger collapsed mx-2" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo"></button>
        <button class="btn btn-danger collapsed" type="button" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree"></button>
        </div>
       
    <div id="collapseOne" class="collapse " aria-labelledby="headingOne" data-parent="#accordionExample">
      <div class="card-body">
      <div class="row">
        <div class="col-8">
          <div class="form-group">
              <label for="credait">:</label>
              <input type="text" class="form-control" id="credait" placeholder="12345678932114" readonly/>
          </div>
        </div>
        <div class="col-4">
          <div class="form-group">
              <label for="credait">:</label>
              <input type="text" class="form-control" id="credait" placeholder="813 " readonly/>
          </div>
        </div>
      </div>
      <p class=" text-center text-warning h4"><i class="fas fa-exclamation-circle ">!</i></p>
      <br>
      <p class="text-white">168</p>
      </div>
    </div>
    <div id="collapseTwo" class="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
      <div class="card-body">
        <form action="#">
          <div class="form-group">
              <label for="credait">:</label>
              <input type="text" class="form-control" id="credait" placeholder="" />
          </div>
          <div class="row">
           <div class="col">
              <label for="deadline">:</label>
              <input type="text" class="form-control" id="deadline" placeholder="" />
           </div>
           <div class="col">
              <label for="password">(3:)</label>
              <input type="text" class="form-control" id="password" placeholder="" />
           </div>
          </div>
        </form>
      </div>
    </div>
    <div id="collapseThree" class="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
      <div class="card-body">
        <div class="row">
          <div class="col">
           <div class="form-group">
              <label for="Name">:</label>
              <input type="text" class="form-control" id="Name" placeholder="" />
          </div>
          </div>
          <div class="col">
            <div class="form-group">
              <label for="cel">:</label>
              <input type="text" class="form-control" id="cel" placeholder="" />
              
            </div>
          </div>
        </div>
        <div class="form-group">
              <label for="cus-adress">:</label>
              <input type="text" class="form-control" id="cus-adress" placeholder="" />
        </div>
      </div>
    </div>
</div>

          <!--  -->
          <div class="d-flex justify-content-center mt-3 pt-3">
            <router-link to="/check" class="btn btn-outline-danger rounded-pill"></router-link>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Navbar from "../Navbar";
import Footer from "../Footer";
import Alert from "../AlertMessage";

export default {
  components: {
    Navbar,
    Footer,
    Alert,
  },
  data(){
    return{
    isLoading: false,
    form:{
      user:{
        name:'',
        email:'',
        tel:'',
        adress:'',
      },
      message:''
    },
    }
  },
  methods:{
    CreatOrder(){
      const vm = this;
      const api = `${process.env.APTPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order =vm.form;
      //  vm.isLoading = true;
       this.$validator.validate().then(valid => {
        if (valid) {
        this.$http.post(api,{data:order}).then(response => {
        console.log('',response);
        vm.isLoading = false;
        vm.$router.push(`/check/${response.data.orderId}`);
      });
          
       } else {
       console.log("");
      }
      });
    }
  }
};
</script>

<style scoped>
label {
  color: #ffffff;
  margin-bottom: 0.5em;
}
</style>



